<template>
  <div>
    <br />
    <h1 class="my-5">
      <span><h3>üôã‚Äç‚ôÇÔ∏è üôã‚Äç‚ôÄÔ∏è</h3></span>
      Usuarios
    </h1>
    <div class="d-flex flex-row flex-wrap justify-content-center">
      <div id="for" class="sinFondo">
        <form
          class="form-inline my-2 my-lg-0 d-flex"
          @submit.prevent="buscarUsuario()"
        >
          <input
            id="usr-input"
            class="form-control m-1"
            type="search"
            v-model="busqueda"
            placeholder="Buscar"
            aria-label="busqueda"
          />
          <button class="btn btn-outline-success m-1" type="submit">
            Buscar
          </button>
        </form>
        <button class="btn btn-outline-success m-1" @click="play()" id="micro">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="16"
            height="16"
            fill="currentColor"
            class="bi bi-mic-fill"
            viewBox="0 0 16 16"
          >
            <path
              d="M13 8c0 .564-.094 1.107-.266 1.613l-.814-.814A4.02 4.02 0 0 0 12 8V7a.5.5 0 0 1 1 0v1zm-5 4c.818 0 1.578-.245 2.212-.667l.718.719a4.973 4.973 0 0 1-2.43.923V15h3a.5.5 0 0 1 0 1h-7a.5.5 0 0 1 0-1h3v-2.025A5 5 0 0 1 3 8V7a.5.5 0 0 1 1 0v1a4 4 0 0 0 4 4zm3-9v4.879L5.158 2.037A3.001 3.001 0 0 1 11 3z"
            />
            <path
              d="M9.486 10.607 5 6.12V8a3 3 0 0 0 4.486 2.607zm-7.84-9.253 12 12 .708-.708-12-12-.708.708z"
            />
          </svg>
        </button>
      </div>
      <div
        id="select"
        class="d-flex flex-wrap align-items-center justify-content-center m-2"
      >
        <div class="d-flex align-items-center m-2">
          <label for="filtro1" class="m-2">
            Orden
          </label>
          <select
            name="filtro1"
            class="form-control usr-input"
            v-model="orden1"
            id="filtro1"
            @change="cambiarEnlace()"
          >
            <option value="nin">Ninguna</option>
            <option value="alfb">Alfab√©ticamente</option>
            <option value="reves">Rev√©s</option>
          </select>
        </div>
        <div class="d-flex align-items-center m-2">
          <label for="filtro2" class="m-2">
            Filtrar
          </label>
          <select
            name="filtro2"
            class="form-control usr-input"
            v-model="orden2"
            id="filtro2"
            @change="cambiarEnlace()"
          >
            <option value="to">Todos</option>
            <option value="ta">Tatuadores</option>
            <option value="cl">Clientes</option>
          </select>
        </div>
      </div>
    </div>
    <b-button
      pill
      class="m-2"
      variant="success"
      size="lg"
      block
      @click="agregarUsuario()"
    >
      A√±adir usuario
    </b-button>
    <div v-if="usuarios.length > 0">
      <paginate ref="paginator" name="usuarios" :list="usuarios" :per="10">
        <table class="table table-striped container mb-5">
          <thead>
            <tr>
              <th scope="col">Perfil</th>
              <th scope="col">Nombre</th>
              <th scope="col">Nick</th>
              <th scope="col">Accion</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in paginated('usuarios')" :key="index">
              <td>
                <div class="imagen">
                  <img
                    class="rounded-circle"
                    :src="src + item.fotoPerfil"
                    @click="perfil(item._id)"
                    alt="fotoPerfil"
                  />
                </div>
              </td>
              <td>{{ item.nombre }}</td>
              <td>{{ item.usuario }}</td>
              <td>
                <b-dropdown text="üõ†" variant="warning" class="m-2">
                  <b-dropdown-item>
                    <p @click="editarUsuario(item._id)" title="edit">
                      üõ† User Settings
                    </p>
                  </b-dropdown-item>
                  <b-dropdown-item>
                    <p @click="showMsgBox(item._id)" title="delete">
                      üí• Delete User
                    </p>
                  </b-dropdown-item>
                </b-dropdown>
              </td>
            </tr>
          </tbody>
        </table>
      </paginate>
    </div>
    <div v-else>
      <h2>No se han encontrado resultados</h2>
    </div>
    <div class="container">
      <b-alert
        :show="dismissCountDown"
        :variant="mensaje.color"
        @dismissed="dismissCountDown = 0"
        @dismiss-count-down="countDownChanged"
      >
        <p>{{ mensaje.texto }}</p>
        <b-progress
          :variant="mensaje.color"
          :max="dismissSecs"
          :value="dismissCountDown"
          height="4px"
        ></b-progress>
      </b-alert>
    </div>

    <paginate-links
      v-if="usuarios.length > 10"
      for="usuarios"
      :show-step-links="true"
      :simple="{
        prev: '‚óÄ',
        next: '‚ñ∂',
      }"
    ></paginate-links>
  </div>
</template>

<script>
export default {
  data() {
    return {
      src: 'https://tattoo-backend.vercel.app/',
      box: '',
      paginate: ['usuarios'],
      usuarios: [],
      dismissSecs: 5,
      dismissCountDown: 0,
      showDismissibleAlert: false,
      mensaje: {
        color: '',
        texto: '',
      },
      usuario: {
        nombre: '',
        tatuador: Boolean,
        fechaNacimiento: '',
        fotoPerfil: '',
        usuario: '',
        password: '',
        aboutYou: '',
        orden: '',
        orden1: '',
        orden2: '',
        busqueda: '',
      },
    }
  },
  created() {
    if (localStorage.getItem('tatuador') != 'true') {
      this.$router.go(-1)
    } else {
      if (
        window.location.href.split('?or=')[1] == undefined &&
        window.location.href.split('se=')[1] == undefined
      ) {
        window.location.href = '/usuarios?or=ninto'
      } else {
        var valores = window.location.href.split('?or=')[1]
        if (valores == undefined)
          this.busqueda = window.location.href.split('?se=')[1]
        if (valores != undefined) {
          this.orden = valores.split('&se=')[0]
          this.orden1 = this.orden.substring(0, this.orden.length - 2)
          this.orden2 = this.orden.substring(
            this.orden.length - 2,
            this.orden.length,
          )
          this.busqueda = window.location.href.split('se=')[1]
        }
        if (this.busqueda == undefined) this.busqueda = ''
        if (this.orden2 == 'ta') {
          this.listarUsuarios('/usuarios/tatuadores')
        } else if (this.orden2 == 'cl') {
          this.listarUsuarios('/usuarios/clientes')
        } else {
          this.listarUsuarios('/usuarios/listar')
        }
      }
    }
  },
  mounted() {
    this.insertarOpciones(this.orden)
  },
  methods: {
    alerta() {
      this.mensaje.color = 'warning'
      this.mensaje.texto = 'Usuario eliminado üí• '
      this.showAlert()
    },
    agregarUsuario() {
      window.location.href = '/agregarUsuario'
    },
    insertarOpciones(valor) {
      document.getElementById('filtro1').value = valor.substring(
        0,
        valor.length - 2,
      )
      document.getElementById('filtro2').value = valor.substring(
        valor.length - 2,
        valor.length,
      )
    },
    buscarUsuario() {
      if (
        this.orden == '' &&
        (this.busqueda == '' || this.busqueda == undefined)
      ) {
        window.location.href = '/'
      } else if (this.orden == '') {
        window.location.href = '?se=' + this.busqueda
      } else if (
        this.orden != '' &&
        (this.busqueda == '' || this.busqueda == undefined)
      ) {
        window.location.href = '?or=' + this.orden
      } else {
        window.location.href = '?or=' + this.orden + '&se=' + this.busqueda
      }
    },
    cambiarEnlace() {
      var valor = this.orden1 + this.orden2
      if (valor == '' && this.busqueda != '') {
        window.location.href = '?se=' + this.busqueda
      } else if (valor == '' && this.busqueda == '') {
        window.location.href = '/'
      } else if (valor != '' && this.busqueda != '') {
        window.location.href = '?or=' + valor + '&se=' + this.busqueda
      } else {
        window.location.href = '?or=' + valor
      }
    },
    listarUsuarios(ruta) {
      this.axios
        .get(ruta)
        .then((res) => {
          var varaux = res.data
          if (this.busqueda == null || this.busqueda == '') {
            if (this.orden1 == 'alfb') {
              this.ordenarNombre(varaux)
              this.usuarios = varaux
            } else if (this.orden1 == 'reves') {
              this.ordenarNombre(varaux)
              varaux.reverse()
              this.usuarios = varaux
            } else {
              this.usuarios = varaux
            }
          } else {
            for (var i = varaux.length - 1; i >= 0; i--) {
              if (
                varaux[i].nombre
                  .toLowerCase()
                  .indexOf(this.busqueda.toLowerCase()) == -1
              ) {
                varaux.splice(i, 1)
              }
            }
            if (this.orden1 == 'alfb') {
              this.ordenarNombre(varaux)
              this.usuarios = varaux
            } else if (this.orden1 == 'reves') {
              this.ordenarNombre(varaux)
              varaux.reverse()
              this.usuarios = varaux
            } else {
              this.usuarios = varaux
            }
          }
        })
        .catch((e) => {
          console.log(e.response)
        })
    },
    ordenarNombre(nombres) {
      nombres.sort(function (a, b) {
        var nombreA = a.nombre.toUpperCase()
        var nombreB = b.nombre.toUpperCase()
        if (nombreA < nombreB) {
          return -1
        }
        if (nombreA > nombreB) {
          return 1
        }
        return 0
      })
    },
    editarUsuario(id) {
      window.location.href = '/usuarios/editar?id=' + id
    },
    eliminarUsuario(id) {
      const usuario = this.usuarios.find((item) => item._id === id)
      this.axios
        .put('usuarios/eliminarFoto/' + id, usuario)
        .then((res) => {
           this.axios.delete('/usuarios/borrar/' + id).then((res) => {
            const index = this.usuarios.findIndex(
              (item) => item._id === res.data._id,
            )
            this.usuarios.splice(index, 1)
            this.alerta();
        })
        .catch((e) => {
            console.log(e);
          })
        })
    },
    perfil(id) {
      window.location.href = '/usuario?id=' + id
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs
    },
    showMsgBox(id) {
      this.boxTwo = ''
      this.$bvModal
        .msgBoxConfirm('¬øQuieres eliminarlo?', {
          title: 'Por favor, confirme',
          size: 'sm',
          buttonSize: 'sm',
          okVariant: 'danger',
          okTitle: 'ELIMINAR',
          cancelTitle: 'CANCELAR',
          footerClass: 'p-2',
          hideHeaderClose: false,
          backgroundColor: 'success',
          centered: true,
        })
        .then((value) => {
          this.box = value
          if (this.box == true) {
            this.eliminarUsuario(id)
          }
        })
        .catch((err) => {
          console.log(err)
        })
    },
    play() {
      document.querySelector('input#usr-input').focus()
      var microfono = document.getElementById('micro')
      var microAbierto = this.microAbierto()
      var microCerrado = this.microCerrado()
      const SpeechRecognition = webkitSpeechRecognition
      const recognition = new SpeechRecognition()
      var valor = ''
      recognition.start()

      recognition.onstart = function () {
        document.querySelector('input#usr-input').value = 'Escuchando...'
        microfono.innerHTML = microAbierto
      }

      recognition.onspeechend = function () {
        microfono.innerHTML = microCerrado
        recognition.stop()
      }
      recognition.onerror = function (event) {
        document.querySelector('input#usr-input').value = ''
      }

      recognition.onresult = function (e) {
        var transcript = e.results[0][0].transcript
        valor = `${transcript}`
        document.querySelector('input#usr-input').value = valor
        document.querySelector('input#usr-input').nextElementSibling.focus()
      }
      for (var i = 0; i < 10; i++) {
        setTimeout(() => {
          this.busqueda = valor
        }, 500 * i)
      }
    },
    microAbierto() {
      return (
        '<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-mic" viewBox="0 0 16 16">' +
        '<path d="M3.5 6.5A.5.5 0 0 1 4 7v1a4 4 0 0 0 8 0V7a.5.5 0 0 1 1 0v1a5 5 0 0 1-4.5 4.975V15h3a.5.5 0 0 1 0 1h-7a.5.5 0 0 1 0-1h3v-2.025A5 5 0 0 1 3 8V7a.5.5 0 0 1 .5-.5z"/>' +
        '<path d="M10 8a2 2 0 1 1-4 0V3a2 2 0 1 1 4 0v5zM8 0a3 3 0 0 0-3 3v5a3 3 0 0 0 6 0V3a3 3 0 0 0-3-3z"/>' +
        '</svg>'
      )
    },
    microCerrado() {
      return (
        '<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-mic-fill" viewBox="0 0 16 16">' +
        '<path d="M13 8c0 .564-.094 1.107-.266 1.613l-.814-.814A4.02 4.02 0 0 0 12 8V7a.5.5 0 0 1 1 0v1zm-5 4c.818 0 1.578-.245 2.212-.667l.718.719a4.973 4.973 0 0 1-2.43.923V15h3a.5.5 0 0 1 0 1h-7a.5.5 0 0 1 0-1h3v-2.025A5 5 0 0 1 3 8V7a.5.5 0 0 1 1 0v1a4 4 0 0 0 4 4zm3-9v4.879L5.158 2.037A3.001 3.001 0 0 1 11 3z"/>' +
        '<path d="M9.486 10.607 5 6.12V8a3 3 0 0 0 4.486 2.607zm-7.84-9.253 12 12 .708-.708-12-12-.708.708z"/>' +
        '</svg>'
      )
    },
  },
}
</script>

<style>
.usr-input {
  color: #243241;
  background-color: #4e647a !important;
}
select#usr-input {
  width: 100px;
}
div#select > div > label {
  width: 50px;
  margin-left: 20px;
}

  /* Fotos de usuarios */
#app > div > div > ul > table > tbody > tr > td > div > img {
  width: 60px !important;
  height: 60px !important;
  object-fit: cover;
}
#app > div > div > ul > table > tbody > tr > td > div > img:hover {
  transform: scale(1.2);
  border: 1px solid #328b63;
}
table {
  background-color: #82a5c7;
  color: #243241;
  border-radius: 10px;
}
a {
  color: #328b63;
  text-decoration: none;
}
#app > div > div > ul > table > tbody > ul > tr > td > div {
  height: 38px;
}

#app > div > div > ul > table > tbody > tr > td {
  padding-left: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
}
#app > div > div > ul > table > tbody > tr > td:nth-child(2),
#app > div > div > ul > table > tbody > tr > td:nth-child(3) {
  padding-top: 20px;
}
#app > div > div > ul > table > tbody > tr:last-child {
  border-bottom: #152230 1px solid;
}
button.btn.dropdown-toggle {
  height: 38px;
}
/* Paginacion */

.paginate-links {
  width: 100%;
  list-style: none;
  text-align: center;
  margin-top: 70px;
}
.paginate-links li {
  width: 70px;
  display: inline;
  background-color: #82a5c7;
  color: #243241;
  padding: 10px;
  margin-left: 0.3rem;
  margin-right: 0.3rem;
  border-radius: 3px;
}
.paginate-links li:hover {
  color: rgb(200, 255, 0) !important;
  cursor: pointer;
}
.paginate-result {
  width: 100%;
  text-align: center;
  margin-bottom: 1rem;
}
ul {
  padding-left: 0em;
}
</style>
